import { useNavigate } from 'react-router-dom';

export default function Learn() {
  const navigate = useNavigate();

  return (
    <div style={{
      padding: '40px 20px',
      minHeight: '100vh',
      textAlign: 'center',
      backgroundSize: 'cover',
      backgroundPosition: 'center',
      color: 'white',
      fontFamily: 'Nunito, sans-serif',
      textShadow: '1px 1px 5px rgba(0,0,0,0.8)'
    }}>
      <div style={{
        maxWidth: '800px',
        margin: '0 auto',
        padding: '30px',
        backgroundColor: 'rgba(0, 0, 0, 0.7)',
        borderRadius: '16px',
        boxShadow: '0 10px 30px rgba(0,0,0,0.4)',
        textAlign: 'center'
      }}>
        <h2 style={{ 
          marginBottom: '20px', 
          fontSize: '28px',
          color: '#3498db',
          textShadow: '0 0 10px #3498db'
        }}>
              
        </h2>

        <div style={{
          textAlign: 'left',
          fontSize: '16px',
          marginBottom: '30px'
        }}>
          <div style={{ marginBottom: '40px' }}>
            <h3 style={{ 
              color: '#3498db',
              fontSize: '24px',
              marginBottom: '20px',
              textAlign: 'center'
            }}>
              
            </h3>
            <div style={{ marginBottom: '30px' }}>
              <h4 style={{ 
                color: '#2ecc71',
                fontSize: '20px',
                marginBottom: '15px',
                textAlign: 'center'
              }}>
                 
              </h4>
              
              <div style={{ 
                backgroundColor: 'rgba(52, 152, 219, 0.2)',
                padding: '20px',
                borderRadius: '10px',
                marginBottom: '20px'
              }}>
                <ol style={{ 
                  paddingLeft: '20px',
                  lineHeight: '1.6'
                }}>
                  <li style={{ marginBottom: '10px' }}>
                    <strong> :</strong>    (  )       .
                  </li>
                  <li style={{ marginBottom: '10px' }}>
                    <strong> :</strong>    ,            .
                  </li>
                  <li style={{ marginBottom: '10px' }}>
                    <strong>:</strong> ,    (     ,      ), .
                  </li>
                  <li style={{ marginBottom: '10px' }}>
                    <strong>:</strong> ,        , .
                  </li>
                </ol>
              </div>
            </div>
            
            <div style={{ marginBottom: '30px' }}>
              <h4 style={{ 
                color: '#2ecc71',
                fontSize: '20px',
                marginBottom: '15px',
                textAlign: 'center'
              }}>
                 
              </h4>
              <div style={{ 
                marginBottom: '25px',
                backgroundColor: 'rgba(52, 152, 219, 0.1)',
                padding: '15px',
                borderRadius: '8px'
              }}>
                <h5 style={{ 
                  color: '#3498db',
                  fontSize: '18px',
                  marginBottom: '10px'
                }}>
                   1: 
                </h5>
                <ul style={{ 
                  paddingLeft: '20px',
                  lineHeight: '1.6'
                }}>
                  <li style={{ marginBottom: '8px' }}>
                    <strong>:</strong>       1  k  (,   )
                  </li>
                </ul>
              </div>
              <div style={{ 
                marginBottom: '25px',
                backgroundColor: 'rgba(231, 76, 60, 0.1)',
                padding: '15px',
                borderRadius: '8px'
              }}>
                <h5 style={{ 
                  color: '#e74c3c',
                  fontSize: '18px',
                  marginBottom: '10px'
                }}>
                   2:  
                </h5>
                <ul style={{ 
                  paddingLeft: '20px',
                  lineHeight: '1.6'
                }}>
                  <li style={{ marginBottom: '8px' }}>
                    <strong>:</strong>       a  b  (,   )
                  </li>
                </ul>
              </div>
              <div style={{ 
                marginBottom: '25px',
                backgroundColor: 'rgba(46, 204, 113, 0.1)',
                padding: '15px',
                borderRadius: '8px'
              }}>
                <h5 style={{ 
                  color: '#2ecc71',
                  fontSize: '18px',
                  marginBottom: '10px'
                }}>
                   3: 
                </h5>
                <ul style={{ 
                  paddingLeft: '20px',
                  lineHeight: '1.6'
                }}>
                  <li style={{ marginBottom: '8px' }}>
                    <strong>:</strong>       1  k ,  
                  </li>
                </ul>
              </div>
              <div style={{ 
                marginBottom: '25px',
                backgroundColor: 'rgba(155, 89, 182, 0.1)',
                padding: '15px',
                borderRadius: '8px'
              }}>
                <h5 style={{ 
                  color: '#9b59b6',
                  fontSize: '18px',
                  marginBottom: '10px'
                }}>
                   4:   
                </h5>
                <ul style={{ 
                  paddingLeft: '20px',
                  lineHeight: '1.6'
                }}>
                  <li style={{ marginBottom: '8px' }}>
                    <strong>:</strong>       a  b ,  
                  </li>
                </ul>
              </div>
              
              <div style={{ 
                marginBottom: '25px',
                backgroundColor: 'rgba(243, 156, 18, 0.1)',
                padding: '15px',
                borderRadius: '8px'
              }}>
                <h5 style={{ 
                  color: '#f39c12',
                  fontSize: '18px',
                  marginBottom: '10px'
                }}>
                   5: 
                </h5>
                <ul style={{ 
                  paddingLeft: '20px',
                  lineHeight: '1.6'
                }}>
                  <li style={{ marginBottom: '8px' }}>
                    <strong>:</strong>     :
                    <ul style={{ paddingLeft: '20px', marginTop: '5px' }}>
                      <li style={{ marginBottom: '5px' }}>1  </li>
                      <li style={{ marginBottom: '5px' }}>2   (  )</li>
                      <li>3 ,  </li>
                    </ul>
                  </li>
                </ul>
              </div>
            </div>
          </div>
          
          <div style={{ marginBottom: '40px' }}>
            <h3 style={{ 
              color: '#3498db',
              fontSize: '24px',
              marginBottom: '20px',
              textAlign: 'center'
            }}>
                 
            </h3>
            
            <div style={{ marginBottom: '30px' }}>
              <h4 style={{ 
                color: '#2ecc71',
                fontSize: '20px',
                marginBottom: '15px',
                textAlign: 'center'
              }}>
                   
              </h4>
              
              <div style={{ 
                marginBottom: '25px',
                backgroundColor: 'rgba(52, 152, 219, 0.1)',
                padding: '15px',
                borderRadius: '8px'
              }}>
                <h5 style={{ 
                  color: '#3498db',
                  fontSize: '18px',
                  marginBottom: '10px'
                }}>
                  
                </h5>
                <ul style={{ 
                  paddingLeft: '20px',
                  lineHeight: '1.6'
                }}>
                  <li style={{ marginBottom: '8px' }}>
                    <strong> :</strong>          
                  </li>
                  <li style={{ marginBottom: '8px' }}>
                    <strong> :</strong>        
                  </li>
                  <li style={{ marginBottom: '8px' }}>
                    <strong>:</strong>    ,      
                  </li>
                  <li style={{ marginBottom: '8px' }}>
                    <strong> :</strong>    ,    
                  </li>
                </ul>
              </div>
              
              <div style={{ 
                backgroundColor: 'rgba(52, 152, 219, 0.1)',
                padding: '15px',
                borderRadius: '8px'
              }}>
                <h5 style={{ 
                  color: '#3498db',
                  fontSize: '18px',
                  marginBottom: '10px'
                }}>
                   
                </h5>
                <ul style={{ 
                  paddingLeft: '20px',
                  lineHeight: '1.6'
                }}>
                  <li style={{ marginBottom: '8px' }}>
                    <strong> :</strong>              -
                  </li>
                  <li style={{ marginBottom: '8px' }}>
                    <strong>:</strong>         
                  </li>
                </ul>
              </div>
            </div>
            
            <div style={{ marginBottom: '30px' }}>
              <h4 style={{ 
                color: '#2ecc71',
                fontSize: '20px',
                marginBottom: '15px',
                textAlign: 'center'
              }}>
                     
              </h4>
              
              <div style={{ 
                marginBottom: '25px',
                backgroundColor: 'rgba(52, 152, 219, 0.1)',
                padding: '15px',
                borderRadius: '8px'
              }}>
                <h5 style={{ 
                  color: '#3498db',
                  fontSize: '18px',
                  marginBottom: '10px'
                }}>
                   1: 
                </h5>
                <ul style={{ 
                  paddingLeft: '20px',
                  lineHeight: '1.6'
                }}>
                  <li style={{ marginBottom: '8px' }}>
                    <strong>:</strong>
                    <ul style={{ paddingLeft: '20px', marginTop: '5px' }}>
                      <li style={{ marginBottom: '5px' }}>      (k+1)</li>
                    </ul>
                  </li>
                  <li style={{ marginBottom: '8px' }}>
                    <strong> :</strong>
                    <ul style={{ paddingLeft: '20px', marginTop: '5px' }}>
                      <li style={{ marginBottom: '5px' }}> n % (k+1) == 0,  ,  ,     </li>
                      <li style={{ marginBottom: '5px' }}>   ,  ,  ,  n % (k+1) </li>
                      <li style={{ marginBottom: '5px' }}>    ,     (k+1)</li>
                    </ul>
                  </li>
                </ul>
              </div>
              
              <div style={{ 
                marginBottom: '25px',
                backgroundColor: 'rgba(231, 76, 60, 0.1)',
                padding: '15px',
                borderRadius: '8px'
              }}>
                <h5 style={{ 
                  color: '#e74c3c',
                  fontSize: '18px',
                  marginBottom: '10px'
                }}>
                   2:  
                </h5>
                <ul style={{ 
                  paddingLeft: '20px',
                  lineHeight: '1.6'
                }}>
                  <li style={{ marginBottom: '8px' }}>
                    <strong>:</strong>
                    <ul style={{ paddingLeft: '20px', marginTop: '5px' }}>
                      <li style={{ marginBottom: '5px' }}>      (a+b)</li>
                    </ul>
                  </li>
                  <li style={{ marginBottom: '8px' }}>
                    <strong> :</strong>
                    <ul style={{ paddingLeft: '20px', marginTop: '5px' }}>
                      <li style={{ marginBottom: '5px' }}>
                        {" n % (a+b) < a,  ,  ,    "}
                      </li>
                      <li style={{ marginBottom: '5px' }}>
                        {"   ,  ,   (n % (a+b) - a + 1) "}
                      </li>
                      <li style={{ marginBottom: '5px' }}>
                            ,     (a+b)
                      </li>
                    </ul>
                  </li>
                </ul>
              </div>
              
              <div style={{ 
                marginBottom: '25px',
                backgroundColor: 'rgba(46, 204, 113, 0.1)',
                padding: '15px',
                borderRadius: '8px'
              }}>
                <h5 style={{ 
                  color: '#2ecc71',
                  fontSize: '18px',
                  marginBottom: '10px'
                }}>
                   3-5
                </h5>
                <ul style={{ 
                  paddingLeft: '20px',
                  lineHeight: '1.6'
                }}>
                  <li style={{ marginBottom: '8px' }}>
                    <strong> :</strong>
                    <ul style={{ paddingLeft: '20px', marginTop: '5px' }}>
                      <li style={{ marginBottom: '5px' }}>  -    </li>
                      <li style={{ marginBottom: '5px' }}> XOR   -  </li>
                      <li style={{ marginBottom: '5px' }}> -  ,   XOR   0</li>
                    </ul>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>

        <button
          onClick={() => navigate(-1)}
          style={{
            padding: '12px 24px',
            fontSize: '18px',
            backgroundColor: '#3498db',
            color: 'white',
            border: 'none',
            borderRadius: '8px',
            cursor: 'pointer',
            fontWeight: 'bold',
            boxShadow: '0 4px 10px rgba(0,0,0,0.3)',
            transition: 'all 0.2s',
            width: '100%'
          }}
          onMouseOver={e => {
            e.target.style.transform = 'scale(1.02)';
            e.target.style.boxShadow = '0 6px 15px rgba(52,152,219,0.5)';
          }}
          onMouseOut={e => {
            e.target.style.transform = 'scale(1)';
            e.target.style.boxShadow = '0 4px 10px rgba(0,0,0,0.3)';
          }}
        >
          
        </button>
      </div>
    </div>
  );
}